<template>
  <div>
    <van-sticky>
      <van-nav-bar title="转账记录" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- <div class="creditTop">
      <span class="haveCredit">可用积分 {{ topCredit | numFilter }}</span>
    </div> -->
    <!-- 主体内容 -->
     <div class="creditCon">
      <!-- <div class="conTitle">积分明细</div> -->
      <!-- <van-list v-model="loading" :finished="finished" finished-text="没有更多了" > -->
          <van-tabs v-model="active" background="white" @change="change">
        <van-tab :title="item" v-for="(item,index1) in arr1" :key="index1"> 
              <!-- <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad"> -->
        <div class="conWrap">
          <div class="creditItem" v-for="(item, index) in creditList" :key="index">
            <div class="lightEl">
              <div class="itemPrice"  v-if="type==1">{{ item.member }}</div>
              <div class="itemPrice"  v-if="type==2">{{ item.a_member }}</div>
              <div class="itemPrice">{{ item.money }} </div>
            </div>
               <div class="lightEl">
              <div class="itemTime" v-if="type==1">转入账号:{{ item.a_member }}</div>
              <div class="itemTime" v-if="type==2">转出账号:{{ item.member }}</div>
              <div class="itemTime">{{ item.create_time }}</div>
            </div>
       
          </div>
        </div>
        <!-- </van-list> -->
        </van-tab>
      </van-tabs>
      <!-- </van-list> -->
    </div>
  </div>
</template>
<script>
import { rewardList, userCenter, transferRecord } from "@/api/personal";
export default {
  data() {
    return {
      arr1: ["转出", "转入"],
        loading: false,
      finished: false,
      active: 0,
      creditList: [],
      config: {},
      topCredit: 0,
        type: 1
    };
  },
  created() {
    // this.getList();
    //    userCenter().then(res=>{
    //      this.topCredit=res.integral;
    //   })
    // integralConfig().then(res => {
    //   this.config = res;
    // });
    //   transferRecord().then(res=>{
    //       console.log(res)
    //                               this.creditList = res
    //   });
    this.onLoad()
  },
  methods: {
        onLoad() {
       transferRecord({type:this.type}).then(res=>{
               
                                  this.creditList = res
      });
        },
    change(item) {
      if (item == 0) {
          this.creditList =[]
         this.type=1
        this.onLoad();
      } else {
          this.creditList =[]
       this.type=2
        this.onLoad();
      }
    },
    toget() {
      this.$router.push("tekmoney");
    },
    getList() {
      rewardList(this.isquery).then(res => {
        this.topCredit = res.integral;
        this.creditList = res.data;
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>

.creditTop {
  width: 100%;
  height: 115px;
  background: url(../../assets/imgs/personal/teamBg.png) 0 0 no-repeat;
  background-size: 100% 100%;
  padding: 25px 20px 0;
  box-sizing: border-box;
  font-size: 18px;
  color: #fff;
  .tips {
    font-size: 12px;
    width: 50px;
    text-align: center;
    height: 25px;
    line-height: 25px;
    background: #28a6ff;
    color: white;
    display: block;
  }
}
.creditCon {
  background: #fff;
  width: 100%;
  height: 100%;
  //   border-radius: 9.5px 9.、5px 0 0;
}
.conTitle {
  color: #333;
  font-size: 18px;
  padding: 12px 0 5px 17px;
}
.conWrap {
  .creditItem {
    margin: 0 15px;
    padding: 19px 0 8.5px;
    font-size: 13px;
    // display: flex;
    overflow: hidden;
    // justify-content: space-between;
    border-bottom: 1px solid #bfbfbf;
    color: #333;
    font-size: 13px;
    .lightEl{
display: flex;justify-content: space-between;
    }
    .itemBtm {
      text-align: right;
    }
    .itemTime {
      color: #999;
      font-size: 11px;
      margin-top: 15px;
    }
  }
}

</style>
